 @import '../../assets/scss/common/mixin';

.iconBox{
  a,
    button {
      display: flex;
      margin-right: 10px;
      width: 28px;
      height: 28px;
  
      i {
        display: flex;
        justify-content: center;
        font-size: 1.5rem;
        color: #888;
        transition: .3s ease-in-out;
  
        &.instagram {
          &:hover {
            color: #c23b3b;
          }
        }
  
        &.kakao {
          img {
            @include image-cover();
            transform: scale(.9);
          }
  
          &:hover {
            color: #dc8a25;
          }
        }
  
        &.message {
          &:hover {
            color: #b07eef;
          }
        }
  
        &.like {
          &:hover {
            color: #f1b7b7;
          }
        }
      }
    }
}

.likeEffect {
  position: absolute;
  top: -20px;
  right: 17px;
  font-size: 1.5rem;
  opacity: 0;
  color: #f1b7b7;
  animation: floatingHeart 3s ease-in-out infinite;

  span {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    color: #fff;
    font-size: .5rem;
    transform: translate(-50%, -50%);
  }
}


@keyframes floatingHeart {
  0% {
    top: 0;
    color: #f1b7b7;
    opacity: 0;
    transform: scale(0) rotate(0);
  }

  25% {
    color: #ef7a7a;
    transform: rotate(10deg);
  }

  50% {
    color: #eecb3d;
    opacity: 1;
    transform: scale(1.3) rotate(-10deg);
  }

  75% {
    color: #99ef48;
    transform: rotate(10deg);
  }

  100% {
    color: #cd5ada;
    top: -100px;
    opacity: 0;
    transform: scale(0.5) rotate(-10deg);
  }
}

